.shops {
  &__divider {
    width: 240px;
    height: 1px;
    background: #000;
    margin: 0 auto;
  }

  .wrapper__search {
    display: grid;
    grid-template: 30px / 285px 412px 100px;
    column-gap: 20px;
    justify-content: center;
    margin: 60px auto 0 auto;
  }

  &__search {
    input {
      width: 180px;
      height: 30px;
      margin-left: 18px;
      background: #FFFFFF;
      box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
      border-radius: 4px;
      border: 0;
      font-size: 12px;
      line-height: 17px;
      text-align: center;
      color: rgba(0, 0, 0, 0.5);
    }

    label {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__button {
    span {
      font-size: 14px;
      line-height: 20px;
      margin-right: 15px;
    }

    &_item {
      margin-left: 5px;
      width: 75px;
      height: 30px;
      background: #FFFFFF;
      box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
      border-radius: 0px;
      border: 0;
      transition: all .2s;

      &_active {
        box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.45);
      }

      &:hover {
        box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.45);
      }
    }
  }

  &__shopping {
    padding-left: 10px;

    span {
      position: relative;

      img {
        position: absolute;
        top: 3px;
        right: 7px;
        width: 20px;
        height: 20px;
      }
    }
  }

  &__products {
    min-height: 670px;
    padding: 60px 0 30px 0;
    display: grid;
    grid-template: repeat(2, 260px) / repeat(3, 220px);
    gap: 70px 60px;
    justify-content: center;
    align-content: center;
  }
}
